/*
ROOT
*/
:root {
    --highlight: #2cace3;
    --background: #f4f4f4;
    --highlight-alt: #19b4e5;
    --highlight2: #EE1D92;
    --chat: #fcf151;
    --text-muted: #505050;
    --text-alert: #ff0000;
    --text-std: #505050;
    --header-size: calc(10.75vh - 1rem);
}

body {
    background-color: #f4f4f4;
    overflow-x: hidden;
    font-size: 10.5pt;
    font-weight: 500;
    font-family: Arial, Helvetica, sans-serif;
}

a {
    color: var(--highlight);
}

.card-header:first-child {
    border-radius: 0;
}

.card {
    border: 0;
    border-radius: 0;
}

.card-body.card-body-video {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
}

.full-width {
    width: 100vw;
    margin-left: calc((100% - 100vw) / 2);
}

button, input[type=button] {
    cursor: pointer;
}


/*
STANDARDS
*/

.drop-shadow {
    -webkit-box-shadow: 0px 3px 5px 0px rgba(140, 140, 140, 1);
    -moz-box-shadow: 0px 3px 5px 0px rgba(140, 140, 140, 1);
    box-shadow: 0px 3px 5px 0px rgba(140, 140, 140, 1);
}

.drop-shadow-small {
    -webkit-box-shadow: 0px 0px 6px 0px rgba(160, 160, 160, 1);
    -moz-box-shadow: 0px 0px 6px 0px rgba(160, 160, 160, 1);
    box-shadow: 0px 0px 6px 0px rgba(160, 160, 160, 1);
}

.drop-shadow-large {
    -webkit-box-shadow: 0px 0px 6px 0px rgba(160, 160, 160, 1);
    -moz-box-shadow: 0px 0px 6px 0px rgba(160, 160, 160, 1);
    box-shadow: 0px 0px 6px 0px rgba(160, 160, 160, 1);
}

a:hover {
    text-decoration: none;
}

p {
    font-size: 12pt;
    /*line-height: 3.55vh;*/
}

.vertical-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
}


/*
BREADCRUMB
*/

nav.breadcrumb {
    /* background-color: var(--background);*/
    background-color: transparent;
    border-radius: 0;
    margin-top: 13px;
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: 22px;
    padding-left: 0;
    font-size: 12pt
}

    nav.breadcrumb > .breadcrumb-item {
        color: var(--text-muted);
        padding-left: 0;
        font-size: 12px;
        text-decoration: underline;
    }

        nav.breadcrumb > .breadcrumb-item + .breadcrumb-item::before {
            color: var(--text-muted);
            content: "|";
            padding-left: 1vh;
            padding-right: 1vh;
            text-decoration: none;
            display: inline-block;
        }

    nav.breadcrumb .breadcrumb-item:last-child {
        color: var(--text-muted);
        text-decoration: none;
    }

.google_conversion_frame {
    display: none;
}

@media (min-width: 768px) {
    .Format_firstModule {
        padding: 0 10px 0 0;
        margin-bottom: 10px;
    }
}

@media (max-width: 768px) {
    .Format_firstModule {
        padding: 0;
        margin-bottom: 10px;
    }
}

.Format_secondModule {
    padding: 0;
    margin-bottom: 10px;
}

.Template-Article {
    display: flex;
    margin: 0;
    border: 2px solid grey;
}

    .Template-Article > div:nth-child(1) {
        background: #fff;
        width: 75pt;
        margin: 10px;
        padding: 50px 0 0;
    }

    .Template-Article > div:nth-child(2) {
        padding: 20px;
        background: #fff;
        flex: 1;
    }

.modal-header {
    display: block;
    text-align: center;
}

    .modal-header h3 {
        display: inline;
        margin: 0 auto !important;
        padding-left: 1.8rem;
    }

    .modal-header button {
        margin-left: 0px !important;
        margin-right: 0px !important;
    }

.nav-tabs.nav-justified {
    width: 100%;
    border-bottom: 0;
    display: flex;
    flex-wrap: wrap;
}

.nav-justified {
    width: 100%;
}

.nav-tabs {
    border-bottom: 1px solid #ddd;
}

.nav {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
}

.cms-tab .nav-tabs > li {
    border: 1px solid #aaa;
    background-color: #ddd;
    line-height: 36px;
    text-align: center;
    font-size: 14pt;
    font-weight: 700;
    color: #595959;
    cursor: pointer;
}

.nav-tabs.nav-justified > li {
    padding: 0;
    float: none;
    display: block;
    width: 100%;
}

.nav > li {
    line-height: 40px;
}


.nav-justified > li {
    padding: 0;
    display: table-cell;
    width: 1%;
    float: none;
}

.nav-tabs > li {
    float: left;
    margin-bottom: -1px;
}

.nav > li {
    position: relative;
    display: block;
}

.cms-tab .nav-tabs.nav-justified > .active > a, .cms-tab .nav-tabs.nav-justified > .active > a:focus, .cms-tab .nav-tabs.nav-justified > .active > a:hover {
    background: #c8e5f7;
}

.nav-tabs.nav-justified > .active > a, .nav-tabs.nav-justified > .active > a:focus, .nav-tabs.nav-justified > .active > a:hover {
    border-bottom-color: #fff;
}

    .nav-tabs.nav-justified > .active > a, .nav-tabs.nav-justified > .active > a:focus, .nav-tabs.nav-justified > .active > a:hover {
        border: 1px solid #ddd;
    }

.nav-tabs > li.active > a, .nav-tabs > li.active > a:focus, .nav-tabs > li.active > a:hover {
    color: #555;
    cursor: default;
    background-color: #fff;
    border: 1px solid #ddd;
    border-bottom-color: transparent;
}

.nav-tabs.nav-justified > li > a {
    border-bottom: 1px solid #ddd;
    border-radius: 4px 4px 0 0;
    margin-right: 0;
    border-radius: 4px;
    margin-bottom: 0;
    text-align: center;
    line-height: 36px;
}

.nav-tabs > li > a:hover {
    border-color: #eee #eee #ddd;
}

.nav > li > a:focus, .nav > li > a:hover {
    text-decoration: none;
    background-color: #eee;
}

.nav-justified > li > a {
    margin: 0;
    text-align: center;
}

.nav-tabs > li > a {
    margin-right: 2px;
    line-height: 1.42857143;
    border: 1px solid transparent;
    border-radius: 4px 4px 0 0;
}

.nav > li > a {
    position: relative;
    display: block;
}

.breadcrumb {
    background-color: transparent;
    padding-bottom: 0;
    margin-bottom: 0;
    padding-left: 0;
    padding: 10px 0px;
    list-style: none;
    border-radius: 4px;
}

    .breadcrumb > .active {
        color: #777;
    }

    .breadcrumb > li {
        display: inline-block;
    }

        .breadcrumb > li + li:before {
            padding: 0 5px;
            color: #ccc;
            content: "/\00a0";
        }

#goog_conv_iframe {
    display: none;
}

