.btn-pcusa {
    display: inline-block;
    font-weight: 400;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    font-size: 1rem;
    background-color: #2cace3;
    padding: 0 20px;
    line-height: 22.5pt;
    height: 24pt;
    border-radius: 0;
    border: 1.5pt solid #2cace3;
    text-align: center;
    transition: color .15s;
    color: white;
}

.btn-pcusa:not(:disabled):not(.disabled) {
    cursor: pointer;
}

.btn-pcusa:hover {
    color: white !important;
    border: 1.5pt solid #1234ff !important;
    background-color: #1234ff !important;
}
/* default buttom End */

.btn-pcusa-gray {
    background-color: #989898;
	border: 1.5pt solid #989898;
}
